import React, { useState } from "react";
//import { useNavigate } from "react-router-dom";
import AuthService from "../../../../utils/services/auth";
import { useHistory } from 'react-router-dom';
import Swal from 'sweetalert2';
import {
  Box,
  Button,
  Checkbox,
  Divider,
  FormControl,
  FormControlLabel,
  FormHelperText,
  Grid,
  IconButton,
  InputAdornment,
  InputLabel,
  makeStyles,
  OutlinedInput,
  Typography
} from '@material-ui/core';
import authService from "../../../../utils/services/auth";

const useStyles = makeStyles((theme) => ({
  root: {},
  redButton: {
    fontSize: '1rem',
    fontWeight: 500,
    backgroundColor: theme.palette.grey[50],
    border: '1px solid',
    borderColor: theme.palette.grey[100],
    color: theme.palette.grey[600],
    textTransform: 'none',
    '&:hover': {
      backgroundColor: theme.palette.primary.light
    },
    [theme.breakpoints.down('sm')]: {
      fontSize: '0.875rem'
    }
  },
  signDivider: {
    flexGrow: 1
  },
  signText: {
    cursor: 'unset',
    margin: theme.spacing(2),
    padding: '5px 56px',
    borderColor: theme.palette.grey[100] + ' !important',
    color: theme.palette.grey[900] + '!important',
    fontWeight: 500
  },
  margin: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(1)
  },
  forgot: {
    textDecoration: 'none',
    color: theme.palette.purple.main
  },
  loginIcon: {
    marginRight: '16px',
    [theme.breakpoints.down('sm')]: {
      marginRight: '8px'
    }
  },
  title: {
    color: theme.palette.grey[600]
  },
  login: {
    backgroundColor: theme.palette.purple.main,
    '&:hover': {
      backgroundColor: theme.palette.purple.dark
    }
  },
  loginput: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    '& > label': {
      top: '23px',
      left: 0,
      color: theme.palette.grey[500],
      '&[data-shrink="false"]': {
        top: '5px'
      }
    },
    '& > div > input': {
      padding: '30.5px 14px 11.5px !important'
    },
    '& legend': {
      display: 'none'
    },
    '& fieldset': {
      top: 0
    }
  },
  startAdornment: {
    color: theme.palette.grey[500],
    marginTop: '18px',
    width: 'auto'
  }
}));

const Login = () => {
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const history = useHistory();
  const classes = useStyles();

//window.alert(AuthService.getCurrentUser().authorities);
  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      await AuthService.login(login, password).then(
        () => {
          if(authService.getCurrentUser().authorities.includes('ADMIN') || authService.getCurrentUser().authorities.includes('RA_N1')){
            history.push(`/dashboard/default`);
          }else if(authService.getCurrentUser().authorities.includes('PLAN_N1')){
            history.push(`/dashboard/planificacao`);
          }else{
            history.push(`/dashboard/home`);
          }
          //
        },
        (error) => {
          console.log(error);

          Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: "Credenciais inv√°lidas, tente novamente!",
        })

        }
      );
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <div>
      <form onSubmit={handleLogin}>

        <FormControl
          fullWidth
          // error={Boolean(touched.email && errors.email)}
          className={classes.loginput}
          variant="outlined"
        >
          <InputLabel htmlFor="outlined-adornment-email-login">Usuario</InputLabel>
          <OutlinedInput
            type="text"
            placeholder="email"
            value={login}
            onChange={(e) => setLogin(e.target.value)}
          />

        </FormControl>

        <FormControl
          fullWidth
          // error={Boolean(touched.password && errors.password)}
          className={classes.loginput}
          variant="outlined"
        >
          <InputLabel htmlFor="outlined-adornment-password-login">Senha</InputLabel>
          <OutlinedInput
            type="password"
            placeholder="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

        </FormControl>
        <Box mt={2}>
          <Button
            disableElevation
            //disabled={isSubmitting}
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            className={classes.login}
          //onclick={handleLogin()}
          >
            Entrar
          </Button>
        </Box>
      </form>
    </div>
  );
};

export default Login;